.snake {
  display: inline-block;
  -webkit-animation: ziksang-spinner-rotate 0.8s infinite linear;
          animation: ziksang-spinner-rotate 0.8s infinite linear;
  border: 4px solid transparent;
  border-radius: 50%;
}
@-webkit-keyframes ziksang-spinner-rotate {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg)
}
}
@keyframes ziksang-spinner-rotate {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg)
}
}


.double-bounce {
  display: inline-block;
  position: relative;
}
.double-bounce1, .double-bounce2 {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background-color: #67CF22;
  opacity: 0.6;
  position: absolute;
  top: 0;
  left: 0;
  -webkit-animation: bounce 2.0s infinite ease-in-out;
  animation: bounce 2.0s infinite ease-in-out;
}
.double-bounce2 {
  -webkit-animation-delay: -1.0s;
  animation-delay: -1.0s;
}
@-webkit-keyframes bounce {
0%, 100% {
    -webkit-transform: scale(0.0)
}
50% {
    -webkit-transform: scale(1.0)
}
}
@keyframes bounce {
0%, 100% {
    transform: scale(0.0);
    -webkit-transform: scale(0.0);
}
50% {
    transform: scale(1.0);
    -webkit-transform: scale(1.0);
}
}






























.toast-bg {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 9998;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.2);
}
.toast {
  position: fixed;
  left: 50%;
  -webkit-transform: translate(-50%, -50%) scale(1);
          transform: translate(-50%, -50%) scale(1);
  word-wrap: break-word;
  padding: 10px;
  text-align: center;
  z-index: 9999;
  font-size: .3rem;
  max-width: 80%;
  color: #fff;
  border-radius: 5px;
  background: rgba(0, 0, 0, 0.7);
  overflow: hidden;
}
.toast i {
  font-style: normal;
}
.toast.middle {
  top: 50%;
}
.toast.top {
  top: 10%;
}
.toast.bottom {
  top: 90%;
}
.leaveBg-enter-active, .leaveBg-leave-active {
  -webkit-transition: opacity .5s;
  transition: opacity .5s
}
.leaveBg-enter, .leaveBg-leave-active {
  opacity: 0;
}
.fade-enter-active, .fade-leave-active {
  -webkit-transition: -webkit-transform .5s;
  transition: -webkit-transform .5s;
  transition: transform .5s;
  transition: transform .5s, -webkit-transform .5s
}
.fade-enter, .fade-leave-active {
  -webkit-transform: translate(-50%, -50%) scale(0);
          transform: translate(-50%, -50%) scale(0);
}

